import React, { Component } from 'react';

import classes from './Contact.module.css'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowRight } from "@fortawesome/free-solid-svg-icons";

class Contact extends Component {
	componentDidMount() {
		window.scrollTo(0, 0);
	}
	
	render () {
		return (
			<div className={classes.Contact}>
				<h2 className={classes.ContactHeader}>Feel free to drop us a note just to say hi or if you would like to collaborate.</h2>
				<form className={classes.ContactForm} action="https://formspree.io/mogdwwpm" method="POST">
					<input className={classes.ContactForm__Inputs} type="text" name="name" placeholder="Name" required />
					<input className={classes.ContactForm__Inputs} type="email" name="_replyto" placeholder="Email" required />
					<input className={classes.ContactForm__Inputs} type="text" name="subject" placeholder="Subject" required />
					<textarea className={[classes.ContactForm__Inputs, classes.ContactForm__Textarea].join(" ")} name="message" placeholder="Message"></textarea>
					<button className={classes.ContactForm__Submit} type="submit">
						<p>Submit</p>
						<p><FontAwesomeIcon icon={faArrowRight} /></p>
					</button>
				</form>
			</div>
		);
	}
}

export default Contact;