import React from 'react';

import classes from './Footer.module.css';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowRight } from "@fortawesome/free-solid-svg-icons";

const footer = (props) => (
	<footer className={classes.Footer}>
		<form className={classes.FooterSignUpForm} action="https://formspree.io/mogdwwpm" method="POST">
			<label className={classes.FooterSignUpLabel} htmlFor={classes.FooterSignUpInput}>Sign Up For Our
				Newsletter</label>
			<div className={classes.FooterSignUpInputContainer}>
				<input className={classes.FooterSignUpInput} type="email" name="_replyto" placeholder="Email Address" required />
				<input type="hidden" name="_subject" value="Sign Up for Refractor Newsletter" required />
				<button className={classes.FooterSignUpSubmit} type="submit"><p><FontAwesomeIcon icon={faArrowRight} /></p></button>
			</div>
		</form>
		<div className={classes.FooterTextContainer}>
			<h4>A mosaic of Asian-American creativity.</h4>
			<h4>©2019</h4>
		</div>
	</footer>
);

export default footer